.swiper {
  width: 100%;
  min-height: 656px;
}
.swiper-button-prev::after,
.swiper-rtl .swiper-button-next::after {
  content: " ";
}
.swiper-button-next::after,
.swiper-rtl .swiper-button-prev::after {
  content: " ";
}
.swiper-button-prev:hover {
  background: linear-gradient(90deg, #ffffff 0%, rgba(255, 255, 255, 0) 100%);
}
.swiper-button-prev:focus {
  background: linear-gradient(90deg, #ffffff 0%, rgba(255, 255, 255, 0) 100%);
}
.swiper-button-prev:active {
  background: linear-gradient(90deg, rgba(120, 89, 207, 0.25) 0%, rgba(120, 89, 207, 0) 100%);
}
.swiper-button-next:hover {
  background: linear-gradient(90deg, rgba(255, 255, 255, 0) 0, #ffffff 100%);
}
.swiper-button-next:focus {
  background: linear-gradient(90deg, rgba(255, 255, 255, 0) 0, #ffffff 100%);
}
.swiper-button-next:active {
  background: linear-gradient(90deg, rgba(120, 89, 207, 0) 0, rgba(120, 89, 207, 0.25) 100%);
}
.swiper-button-prev,
.swiper-button-next {
  width: 19px;
  height: 100%;
  top: 3.2%;
}
.swiper-button-prev path {
  fill: #7859CF;
}
.swiper-button-prev:disabled path {
  fill: #bdbdbd;
}
.swiper-button-next path {
  fill: #7859CF;
}
.swiper-button-next:disabled path {
  fill: #bdbdbd;
}
.swiper-pagination {
  display: none;
}
.swiper-button-next,
.swiper-rtl .swiper-button-prev {
  right: 0;
}
.swiper-button-prev,
.swiper-rtl .swiper-button-next {
  left: 0;
}
@media (min-width: 768px) {
  .swiper {
    min-height: 828px;
  }
  .swiper-button-next,
  .swiper-rtl .swiper-button-prev {
    right: 0;
  }
  .swiper-button-prev,
  .swiper-rtl .swiper-button-next {
    left: 0;
  }
  .swiper-button-prev,
  .swiper-button-next {
    width: 80px;
    height: 639px;
