  padding: 6px 0 6px 52px;
  position: relative;
  font-size: 16px;
  line-height: 24px;
}
.checkbox__span::before {
  position: absolute;
  content: "";
  top: 15%;
  left: 0;
  width: 42px;
  height: 24px;
  background-color: #bdbdbd;
  border-radius: 12px;
  transition: background-color 0.2s ease-in-out;
  cursor: pointer;
}
.checkbox__input + .checkbox__span::after {
  position: absolute;
  content: "";
  width: 20px;
  height: 20px;
  top: 21%;
  left: 1%;
  right: 0;
  bottom: 0;
  border-radius: 50%;
  background-color: #ffffff;
  transition: transform 0.2s ease-in-out;
}
.checkbox__input:hover + .checkbox__span::before,
.checkbox__input:focus + .checkbox__span::before {
  border: 2px solid #7859CF;
  background-color: rgba(120, 89, 207, 0.25);
}
.checkbox__input:hover:checked + .checkbox__span::before,
.checkbox__input:focus:checked + .checkbox__span::before {
  background-color: rgba(120, 89, 207, 0.25);
}
.checkbox__input:checked + .checkbox__span::before {
  background-color: #7859CF;
}
.checkbox__input:checked + .checkbox__span::after {
  transform: translateX(18px);
}
.checkbox__input:disabled + .checkbox__span::before {
  background-color: #E2E2E2;
  border: none;
}
.checkbox__input:disabled + .checkbox__span {
  color: #E2E2E2;
  border: none;
}
.checkbox__input:checked:disabled + .checkbox__span::after {
  background-color: #bdbdbd;
  border: none;
}
